h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.category-books-list {
  padding: 40px 0;
}

@media screen and (min-width: 1440px) {
  .category-books-list {
    padding: 40px;
    padding-top: 0;
  }
}

.category-books-list__container {
  max-width: 100%;
}

.category-books-list__title {
  margin-bottom: 40px;
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;

  margin-left: 10px;
  letter-spacing: -0.04em;
}

@media screen and (min-width: 768px) {
  .category-books-list__title {
    font-size: 48px;
    line-height: 52px;
  }
}

.paintedWord {
  color: #4f2ee8;
}

.category-books-list__list {
  display: flex;
  flex-direction: column;

  gap: 40px;
}

@media screen and (min-width: 768px) {
  .category-books-list__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 25px;
  }
}

@media screen and (min-width: 1440px) {
  .category-books-list__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}

@media screen and (min-width: 480px) {
  .category-books-list__item {
    max-width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .category-books-list__item {
    max-width: 218px;
  }
}
@media screen and (min-width: 1440px) {
  .category-books-list__item {
    max-width: 180px;
  }
}

.card__container.card {
  min-width: 100%;
  display: flex;
  flex-direction: column;
  gap: 14px;
}
@media screen and (min-width: 480px) {
  .card__container.card {
    max-width: 335px;
  }
  @media screen and (min-width: 768px) {
    .card__container.card {
      width: 218px;
    }
  }
  @media screen and (min-width: 1440px) {
    .card__container.card {
      width: 256px;
    }
  }
}

.card__img-container {
  width: 100%;
  overflow: hidden;
}
@media screen and (min-width: 480px) {
  .card__img-container {
    height: 485px;
  }
}
@media screen and (min-width: 768px) {
  .card__img-container {
    height: 316px;
  }
}
@media screen and (min-width: 1440px) {
  .card__img-container {
    height: 256px;
    width: 180px;
  }
}

.card__img {
  width: 100%;
  border-radius: 8px;
  box-sizing: border-box;
  object-fit: cover;
}

@media screen and (min-width: 480px) {
  .card__img {
    height: 485px;
  }
}
@media screen and (min-width: 768px) {
  .card__img {
    height: 316px;
  }
}
@media screen and (min-width: 1440px) {
  .card__img {
    height: 256px;
  }
}

.img-hover {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 70px;
  background: #eac645;
  border-radius: 0px 0px 8px 8px;
  font-family: 'DM Sans';
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #111111;
  transition-duration: 500ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  opacity: 0;
}

.category-books-list__item:hover .img-hover,
.category-books-list__item:focus .img-hover {
  opacity: 1;
  transform: translateY(-100%);
}

.card__desc {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.card__heading {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.04em;
  text-transform: uppercase;
  color: #ffffff;
}

.card__author {
  font-family: 'DM Sans';
  font-style: italic;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.04em;
  color: #b4afaf;
}
